.content {
    img {
        position: relative;
        display: block;
        max-width: 100%;
        height: auto;
        margin: 0 auto;
        background: none;
        font-family: var(--font__captions);
        text-align: center;

        &:before {
            position: relative;
            z-index: 1;
            top: 50%;
            left: 50%;
            content: attr(alt);
            text-wrap: pretty;
            max-width: 50ch;
            display: block;
            transform: translateX(-50%) translateY(-50%);
        }

        &:after {
            content: "";
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: var(--color__background__alt);
        }
    }
}
